import React from "react";
import { useEffect, useState } from "react";
import Caro from "react-bootstrap/Carousel";

const Carousel = () => {
  const [data, setData] = useState([]);

  let url = "";
  url = `https://www.thesportsdb.com/api/v1/json/3/search_all_teams.php?l=Formula 1`;

  useEffect(() => {
    function fetchData() {
      fetch(url)
        .then((response) => response.json())
        .then((item) => {
          setData(item.teams);
        })
        .catch((err) => {
          console.log(err.message);
        });
    }
    fetchData();
  }, [url]);

  return (
    <Caro>
      {data.map((item, i) => {
        return (
          <Caro.Item interval={1500} key={i}>
            <img className="d-block p-0" src={item.strTeamLogo+"/preview"} srcSet="" alt="" />
          </Caro.Item>
        );
      })}
    </Caro>
  );
};

export default Carousel;
